    function lowerImageTransparency(base64Image, callback) {
      var img = new Image();
      img.onload = function () {
        var canvas = document.createElement("canvas");
        canvas.width = img.width;
        canvas.height = img.height;
    
        var ctx = canvas.getContext("2d");
        ctx.drawImage(img, 0, 0);
    
        var imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
        var data = imageData.data;
    
        for (var i = 0; i < data.length; i += 4) {
          // Reduce the alpha (transparency) value by 50%
          data[i + 3] = Math.floor(data[i + 3] * 0.2);
        }
    
        ctx.putImageData(imageData, 0, 0);
    
        // Convert the modified image to base64 format
        var modifiedBase64Image = canvas.toDataURL("image/png");
    
        callback(modifiedBase64Image);
      };
      img.src = base64Image;
    }

    
