    const handleFileUploadOnFirebaseStorage = async (bucketName, file) => {
      // 1. If no file, return
      if (file === "") return "";

      // 2. Put the file into bucketName
      try {
      const uploadTask = await storageService.ref(`/${bucketName}/${file.name}`).put(file);
   
      
      // 3. Get download URL and return it as 
      return uploadTask.ref.getDownloadURL().then((fileURL) => fileURL);
    } catch (e) {
      console.log(e) //  or    
    }
   };

